import './App.css';

import 'bootstrap/dist/css/bootstrap.min.css';
import { BrowserRouter as Router, Switch, Redirect, Route } from 'react-router-dom';
//import ReactPlayer from 'react-player';

import Header from './comps/Header';
import HomeS1 from './comps/HomeS1';

const App = () => {

    return(
        <Router basename={process.env.PUBLIC_URL}>

            <Switch>

                <Route exact path='/' render={() => 
                    { return (
                        <>
                        <Header homepage={true} />
                        <HomeS1 />
                        </>
                    ); }
                } />
                <Route exact path='/prodotti' render={() => 
                    { return (
                        <Header />
                    ); }
                } />
                <Route exact path='/chisiamo' render={() => 
                    { return (
                        <Header />
                    ); }
                } />
                <Route exact path='/fattoria' render={() => 
                    { return (
                        <Header />
                    ); }
                } />
                <Route exact path='/sociale' render={() => 
                    { return (
                        <Header />
                    ); }
                } />
                <Route exact path='/contatti' render={() => 
                    { return (
                        <Header />
                    ); }
                } />
                <Route><Redirect to='/'/></Route>

            </Switch>
            
        </Router>
    );
}

export default App;