import { useState, useEffect } from 'react';

import useWindowSize from '../useWindowSize';
import ReactPlayer from 'react-player';

import './Home.css';

const HomeS1 = () => {

    const [pW, setPW] = useState();
    const [pH, setPH] = useState();
    
    const size = useWindowSize();

    useEffect(() => {
        let ww = size.width;
        if (ww > 1200)
            ww = size.width * 2 / 5;
        setPW(ww);
        setPH(ww * 9 / 16);
    }, [size]);

    return (
        <section>
            <div className='text-container'>
                <p>
                    Amiamo Madre Natura e vivere in armonia con Essa, ne assecondiamo i Bisogni e
                    accettiamo i Doni che regala, rispettando le Tradizioni e le pratiche Verdi. Offriamo i Frutti di
                    questa meravigliosa Magia a persone interessate alla Qualità, alla Genuinità ed alla
                    Naturalezza, nonché aperte ad una connessione profonda con Madre Natura e i suoi Magici
                    Frutti.
                </p>
            </div>
            <div className='player-container'>

                <ReactPlayer className='player'
                        url={process.env.PUBLIC_URL + '/res/WhatsApp Video 2021-08-30 at 17.26.52.mp4'} 
                        controls={true} playing={false} height={pH} width={pW}
                    />

            </div>
        </section>
    );
}

export default HomeS1;