
import { Link } from 'react-router-dom';

import { useState, useEffect } from 'react';
import useWindowSize from '../useWindowSize';

import { motion } from 'framer-motion';

import './Header.css';

const Header = (props) => {

	const [showMenu, setShowMenu] = useState(false);
	const size = useWindowSize();

	useEffect(() => {
		if (size.width > 1200) {
			document.body.style.overflowY = 'scroll';
			setShowMenu(false);
		}
	}, [size]);

	const wrap_setShowMenu = () => {
		if (!showMenu)
			document.body.style.overflowY = 'hidden';
		else
			document.body.style.overflowY = 'scroll';

		setShowMenu(o => !o);
	}

	const animate = {
        open: {
            opacity: 1,
            translateX: '0px',
            transition: {
                duration: 0.5
            },
            display: 'block'
        },
        close: {
            opacity: 1,
            translateX: '-60vw',
            transition: {
                duration: 0.5
            },
            transitionEnd: {
                display: 'none'
            }
        }
    };
    
	return(
		<div className='head' style={{height: props.homepage ? '80vh' : '30vh'}}>

			<div className='burger' onClick={() => wrap_setShowMenu()}>
				<div className='b-box'></div>
				<div className='b-box'></div>
				<div className='b-box'></div>
			</div>

			<div className='logo'>
				<h1>Monte Spazzavento</h1>
				<h5>Azienda agricola biologica</h5>
			</div>
			
			<motion.div initial='close' animate={showMenu ? 'open' : 'close'} variants={animate} className='menu'>
				<div className='navbar-menu'>
					<Link to='/' style={{textDecoration: 'none'}}><h5>HOME</h5></Link>
					<Link to='/prodotti' style={{textDecoration: 'none'}}><h5>MAGICI FRUTTI</h5></Link>
					<Link to='/chisiamo' style={{textDecoration: 'none'}}><h5>CHI SIAMO</h5></Link>
					<Link to='/fattoria' style={{textDecoration: 'none'}}><h5>FATTORIA DIDATTICA</h5></Link>
					<Link to='/sociale' style={{textDecoration: 'none'}}><h5>IMPEGNO SOCIALE</h5></Link>
					<Link to='/contatti' style={{textDecoration: 'none'}}><h5>CONTATTI</h5></Link>
				</div>
			</motion.div>

			<div className='navbar'>
				<Link to='/' style={{textDecoration: 'none'}}><h5>HOME</h5></Link>
				<Link to='/prodotti' style={{textDecoration: 'none'}}><h5>MAGICI FRUTTI</h5></Link>
				<Link to='/chisiamo' style={{textDecoration: 'none'}}><h5>CHI SIAMO</h5></Link>
				<Link to='/fattoria' style={{textDecoration: 'none'}}><h5>FATTORIA DIDATTICA</h5></Link>
				<Link to='/sociale' style={{textDecoration: 'none'}}><h5>IMPEGNO SOCIALE</h5></Link>
				<Link to='/contatti' style={{textDecoration: 'none'}}><h5>CONTATTI</h5></Link>
			</div>

			{ props.homepage &&
			<div className='presentation'>
				<p>Siamo un’azienda agricola contadina biologica e fattoria didattica.</p>
			</div>
			}
		</div>
	);

}

export default Header;
